.app-prices::before {
  content: "";
  display: block;
  position: absolute;
  background-image: linear-gradient(to top left, transparent 0%, transparent 50%, #ffffff 50%, #ffffff 100%);
  background-repeat: no-repeat;
  min-height: 31px;
  width: 100%;
}

.app-prices {
  background-color: #f2f2f2;
  margin-top: 32px;
  position: relative;
  text-transform: uppercase;
  font-size: 14px;
  line-height: 30px;
  text-align: center;
  padding-bottom: 40px;
}

.app-prices__list {
  padding-top: 91px;
  display: flex;
  color: #283645;
  text-align: center;
  overflow: hidden;
  justify-content: center;
  margin-bottom: 28px;
}

.app-prices__item {
  width: 275px;
  box-sizing: border-box;
  background-color: white;
}

.app-prices__head {
  position: relative;
  background-color: #283645;
  color: #ffffff;
  padding: 17px;
  border: 1px solid white;

  &--hit::after {
    content: "";
    position: absolute;
    width: 74px;
    height: 73px;
    background: url("../img/bg-hit.svg") no-repeat center;
    top: 0;
    left: -2px;
  }
}

.app-prices__price {
  font-weight: 300;
  font-size: 24px;
}

.app-prices__title {
  margin: 0;
  font-size: 18px;
  line-height: 30px;
  margin-top: -1px;
}

.app-prices__buttons {
  display: inline-block;
  margin: 0 auto;
}

.app-prices__button {
  vertical-align: middle;
  width: 25px;
  height: 25px;
  background: none;
  background-color: #bec2c7;
  border: 0 solid transparent;
  border-radius: 90%;
  transform: scale(0.3);
  box-sizing: border-box;
  cursor: pointer;
  margin-left: -3px;

  &--active {
    border: 7px solid #2f3c4b;
    background-color: #ffffff;
    transform: scale(0.5);
  }

  &:nth-child(2) {
    margin-left: -3px;
  }
}

.app-prices__hit {
  position: absolute;
  display: none;
}

.app-prices__head--hit .app-prices__hit {
  display: block;
  top: 11%;
  left: 2.7%;
  z-index: 1;
  transform: rotate(-44deg);
}

.app-prices__button:not(.app-prices__button--active) {
  &:hover,
  &:focus {
    background-color: #7e868f;
  }

  &:active {
    background-color: #e9ebec;
  }
}

@media (min-width: @tablet-width) {
  .app-prices {
    margin-top: 0;
    overflow: hidden;
    text-transform: none;
  }

  .app-prices__wrapper {
    width: 660px;
    padding: 0;
    margin: 0 auto;
    display: flex;
    box-sizing: border-box;
  }

  .app-prices__list {
    padding-top: 73px;
  }

  .app-prices::before {
    height: 51px;
  }

  .app-prices__title {
    font-size: 16px;
    line-height: 30px;
    margin-bottom: -3px;
  }

  .app-prices__price {
    font-size: 18px;
    line-height: 30px;
  }

  .app-prices__item {
    width: 156px;
  }

  .app-prices__head {
    padding: 12px 9px 8px 8px;

    &::after {
      background-size: 96%;
      background-position: top 0 left -9px;
      top: 0;
      left: 0;
    }
  }

  .app-prices__buttons {
    display: none;
  }

  .app-prices__head--hit .app-prices__hit {
    top: 4%;
    left: 4.3%;
  }
}

@media (min-width: @desktop-width) {
  .app-prices::before {
    height: 80px;
  }

  .app-prices__wrapper {
    width: 1200px;
    padding-top: 60px;
    padding-left: 109px;
  }

  .app-prices__price {
    font-size: 24px;
  }

  .app-prices__title {
    font-size: 18px;
    margin-bottom: 2px;
  }

  .app-prices__item {
    width: 235px;
  }

  .app-prices__head {
    padding: 16px 7px 17px;

    &::after {
      background-size: 100%;
      background-position: top 0 left 0;
    }
  }

  .app-prices {
    padding-bottom: 70px;
  }

  .app-prices__head--hit .app-prices__hit {
    top: 13%;
    left: 5%;
  }
}
